'use client';

import { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Users, Zap, TrendingUp, UserPlus } from 'lucide-react';
import { getLiveActivity, type LiveActivity } from '@/lib/api/engagement';

/**
 * SocialProofBar - Shows live platform activity
 * Implements: Social Proof (FOMO + Bandwagon Effect)
 * "23 students in Hyderabad are networking right now"
 */
export default function SocialProofBar({ location }: { location?: string }) {
  const [activity, setActivity] = useState<LiveActivity | null>(null);
  const [currentStat, setCurrentStat] = useState(0);

  const fetchActivity = useCallback(async () => {
    try {
      const data = await getLiveActivity(location);
      setActivity(data);
    } catch (error) {
      console.error('Failed to fetch live activity:', error);
    }
  }, [location]);

  useEffect(() => {
    fetchActivity();
    // Refresh every 30 seconds
    const interval = setInterval(fetchActivity, 30000);
    return () => clearInterval(interval);
  }, [fetchActivity]);

  // Rotate between different stats
  useEffect(() => {
    if (!activity) return;
    const interval = setInterval(() => {
      setCurrentStat((prev) => (prev + 1) % 3);
    }, 5000);
    return () => clearInterval(interval);
  }, [activity]);

  if (!activity) return null;

  const stats = [
    {
      icon: <Users className="w-3.5 h-3.5" />,
      text: `${activity.activeUsersNow} student${activity.activeUsersNow !== 1 ? 's' : ''} networking right now ${activity.locationLabel}`,
      color: 'text-green-600 dark:text-green-400',
      bg: 'bg-green-50 dark:bg-green-900/20',
    },
    {
      icon: <Zap className="w-3.5 h-3.5" />,
      text: `${activity.connectionsToday} connections made today`,
      color: 'text-blue-600 dark:text-blue-400',
      bg: 'bg-blue-50 dark:bg-blue-900/20',
    },
    {
      icon: <UserPlus className="w-3.5 h-3.5" />,
      text: `${activity.newUsersToday} new student${activity.newUsersToday !== 1 ? 's' : ''} joined today`,
      color: 'text-purple-600 dark:text-purple-400',
      bg: 'bg-purple-50 dark:bg-purple-900/20',
    },
  ];

  const current = stats[currentStat];

  return (
    <div className={`${current.bg} rounded-lg px-3 py-2 overflow-hidden`}>
      <AnimatePresence mode="wait">
        <motion.div
          key={currentStat}
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -10 }}
          transition={{ duration: 0.3 }}
          className={`flex items-center gap-2 text-xs font-medium ${current.color}`}
        >
          {/* Pulsing dot */}
          <span className="relative flex h-2 w-2">
            <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-green-400 opacity-75"></span>
            <span className="relative inline-flex rounded-full h-2 w-2 bg-green-500"></span>
          </span>
          {current.icon}
          <span>{current.text}</span>
        </motion.div>
      </AnimatePresence>
    </div>
  );
}
