'use client';

import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Target, MessageSquare, FileText, Users, CheckCircle, X } from 'lucide-react';
import { getWeeklyGoals, getNudges, type WeeklyGoals, type ProgressNudge } from '@/lib/api/engagement';

/**
 * ProgressNudges - Shows incomplete progress bars and tasks
 * Implements: Zeigarnik Effect (Incomplete Tasks Bug You)
 * "You've connected with 7/10 people this week‚Äîfinish strong!"
 */
export default function ProgressNudges() {
  const [goals, setGoals] = useState<WeeklyGoals | null>(null);
  const [nudges, setNudges] = useState<ProgressNudge[]>([]);
  const [dismissedNudges, setDismissedNudges] = useState<Set<string>>(new Set());
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [goalsData, nudgesData] = await Promise.all([
          getWeeklyGoals(),
          getNudges(),
        ]);
        setGoals(goalsData);
        setNudges(nudgesData);
      } catch (error) {
        console.error('Failed to fetch progress:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  const dismissNudge = (type: string) => {
    setDismissedNudges(prev => new Set([...prev, type]));
  };

  if (loading) return null;

  const visibleNudges = nudges.filter(n => !dismissedNudges.has(n.type));

  return (
    <div className="space-y-3">
      {/* Weekly Goals Progress */}
      {goals && !goals.isCompleted && (
        <div className="bg-white dark:bg-gray-900 rounded-xl p-4 border border-gray-200 dark:border-gray-800 shadow-sm">
          <div className="flex items-center gap-2 mb-3">
            <Target className="w-4 h-4 text-blue-500" />
            <h3 className="text-sm font-semibold text-gray-900 dark:text-white">Weekly Goals</h3>
            {goals.isCompleted && (
              <span className="ml-auto px-2 py-0.5 bg-green-100 text-green-700 text-xs rounded-full flex items-center gap-1">
                <CheckCircle className="w-3 h-3" /> Done!
              </span>
            )}
          </div>

          <div className="space-y-3">
            {/* Connections Goal */}
            <GoalBar
              icon={<Users className="w-3.5 h-3.5 text-blue-500" />}
              label="Connections"
              current={goals.connectionsMade}
              target={goals.connectionsTarget}
              progress={goals.connectionsProgress}
              color="blue"
            />

            {/* Posts Goal */}
            <GoalBar
              icon={<FileText className="w-3.5 h-3.5 text-purple-500" />}
              label="Posts"
              current={goals.postsMade}
              target={goals.postsTarget}
              progress={goals.postsProgress}
              color="purple"
            />

            {/* Messages Goal */}
            <GoalBar
              icon={<MessageSquare className="w-3.5 h-3.5 text-green-500" />}
              label="Messages"
              current={goals.messagesSent}
              target={goals.messagesTarget}
              progress={goals.messagesProgress}
              color="green"
            />
          </div>
        </div>
      )}

      {/* Progress Nudges */}
      <AnimatePresence>
        {visibleNudges.map((nudge) => (
          <motion.div
            key={nudge.type}
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className="bg-white dark:bg-gray-900 rounded-xl p-3 border border-gray-200 dark:border-gray-800 shadow-sm"
          >
            <div className="flex items-start gap-2">
              <span className="text-lg">{nudge.icon}</span>
              <p className="flex-1 text-sm text-gray-700 dark:text-gray-300">{nudge.message}</p>
              <button
                onClick={() => dismissNudge(nudge.type)}
                className="p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-full flex-shrink-0"
              >
                <X className="w-3.5 h-3.5 text-gray-400" />
              </button>
            </div>

            {/* Progress bar for nudge */}
            {nudge.target > 0 && nudge.progress >= 0 && (
              <div className="mt-2 ml-7">
                <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-1.5">
                  <motion.div
                    className="bg-blue-500 h-1.5 rounded-full"
                    initial={{ width: 0 }}
                    animate={{ width: `${Math.min((nudge.progress / nudge.target) * 100, 100)}%` }}
                    transition={{ duration: 1, delay: 0.3 }}
                  />
                </div>
              </div>
            )}
          </motion.div>
        ))}
      </AnimatePresence>
    </div>
  );
}

function GoalBar({
  icon,
  label,
  current,
  target,
  progress,
  color,
}: {
  icon: React.ReactNode;
  label: string;
  current: number;
  target: number;
  progress: number;
  color: 'blue' | 'purple' | 'green';
}) {
  const colors = {
    blue: 'bg-blue-500',
    purple: 'bg-purple-500',
    green: 'bg-green-500',
  };

  const bgColors = {
    blue: 'bg-blue-100 dark:bg-blue-900/30',
    purple: 'bg-purple-100 dark:bg-purple-900/30',
    green: 'bg-green-100 dark:bg-green-900/30',
  };

  return (
    <div>
      <div className="flex items-center justify-between mb-1">
        <div className="flex items-center gap-1.5">
          {icon}
          <span className="text-xs text-gray-600 dark:text-gray-400">{label}</span>
        </div>
        <span className="text-xs font-medium text-gray-900 dark:text-white">
          {current}/{target}
        </span>
      </div>
      <div className={`w-full ${bgColors[color]} rounded-full h-2`}>
        <motion.div
          className={`${colors[color]} h-2 rounded-full`}
          initial={{ width: 0 }}
          animate={{ width: `${Math.min(progress, 100)}%` }}
          transition={{ duration: 1, delay: 0.2 }}
        />
      </div>
    </div>
  );
}
