'use client';

import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Sparkles, UserPlus, ChevronRight, X, Gem } from 'lucide-react';
import { getDailyMatches, getHiddenGem, type DailyMatch, type DailyMatchesResponse } from '@/lib/api/engagement';
import Link from 'next/link';

/**
 * DailyMatchCard - Shows variable daily match suggestions
 * Implements: Variable Rewards (Slot Machine Trick)
 * Sometimes 1 match, sometimes 5+ ‚Äî unpredictable = more dopamine
 */
export default function DailyMatchCard() {
  const [matchData, setMatchData] = useState<DailyMatchesResponse | null>(null);
  const [hiddenGem, setHiddenGem] = useState<{ match: DailyMatch; message: string } | null>(null);
  const [dismissed, setDismissed] = useState(false);
  const [loading, setLoading] = useState(true);
  const [showSurprise, setShowSurprise] = useState(false);

  useEffect(() => {
    const fetchMatches = async () => {
      try {
        const [matches, gem] = await Promise.all([
          getDailyMatches(),
          getHiddenGem(),
        ]);
        setMatchData(matches);
        if (gem) setHiddenGem(gem as { match: DailyMatch; message: string });

        // Show surprise animation if bonus matches
        if (matches.surpriseMessage) {
          setTimeout(() => setShowSurprise(true), 500);
          setTimeout(() => setShowSurprise(false), 3000);
        }
      } catch (error) {
        console.error('Failed to fetch daily matches:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchMatches();
  }, []);

  if (loading || dismissed || !matchData || matchData.matches.length === 0) return null;

  return (
    <div className="space-y-3">
      {/* Surprise message */}
      <AnimatePresence>
        {showSurprise && matchData.surpriseMessage && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            className="bg-gradient-to-r from-purple-500 to-pink-500 text-white rounded-xl p-3 text-center text-sm font-medium"
          >
            {matchData.surpriseMessage}
          </motion.div>
        )}
      </AnimatePresence>

      {/* Daily Matches */}
      <div className="bg-white dark:bg-gray-900 rounded-xl border border-gray-200 dark:border-gray-800 shadow-sm overflow-hidden">
        <div className="p-3 border-b border-gray-100 dark:border-gray-800 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Sparkles className="w-4 h-4 text-purple-500" />
            <h3 className="text-sm font-semibold text-gray-900 dark:text-white">
              Today&apos;s Matches
            </h3>
            <span className="px-1.5 py-0.5 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 text-xs rounded-full font-medium">
              {matchData.matchCount}
            </span>
          </div>
          <button onClick={() => setDismissed(true)} className="p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-full">
            <X className="w-4 h-4 text-gray-400" />
          </button>
        </div>

        <div className="divide-y divide-gray-100 dark:divide-gray-800">
          {matchData.matches.map((match, index) => (
            <motion.div
              key={match.id}
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: index * 0.1 }}
            >
              <Link
                href={`/profile/${match.username}`}
                className="flex items-center gap-3 p-3 hover:bg-gray-50 dark:hover:bg-gray-800/50 transition-colors"
              >
                {match.profileImage ? (
                  <img
                    src={match.profileImage}
                    alt={match.name}
                    className="w-10 h-10 rounded-full object-cover"
                  />
                ) : (
                  <div className="w-10 h-10 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white text-sm font-bold">
                    {match.name.charAt(0)}
                  </div>
                )}

                <div className="flex-1 min-w-0">
                  <div className="flex items-center gap-2">
                    <p className="text-sm font-medium text-gray-900 dark:text-white truncate">{match.name}</p>
                    {match.isOnline && (
                      <span className="w-2 h-2 bg-green-500 rounded-full flex-shrink-0" />
                    )}
                  </div>
                  {match.headline && (
                    <p className="text-xs text-gray-500 dark:text-gray-400 truncate">{match.headline}</p>
                  )}
                  {match.college && (
                    <p className="text-xs text-gray-400 dark:text-gray-500 truncate">{match.college}</p>
                  )}
                </div>

                <button className="flex items-center gap-1 px-3 py-1.5 bg-blue-50 dark:bg-blue-900/30 text-blue-600 dark:text-blue-400 text-xs font-medium rounded-full hover:bg-blue-100 dark:hover:bg-blue-900/50 transition-colors">
                  <UserPlus className="w-3.5 h-3.5" />
                  Connect
                </button>
              </Link>
            </motion.div>
          ))}
        </div>

        <Link
          href="/find-people"
          className="flex items-center justify-center gap-1 p-2.5 text-xs text-blue-600 dark:text-blue-400 font-medium hover:bg-blue-50 dark:hover:bg-blue-900/20 transition-colors border-t border-gray-100 dark:border-gray-800"
        >
          Find more people <ChevronRight className="w-3.5 h-3.5" />
        </Link>
      </div>

      {/* Hidden Gem (weekly surprise) */}
      {hiddenGem && (
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          className="bg-gradient-to-br from-amber-50 to-yellow-50 dark:from-amber-900/20 dark:to-yellow-900/20 rounded-xl border border-amber-200 dark:border-amber-800 p-4"
        >
          <div className="flex items-start gap-3">
            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-amber-400 to-yellow-500 flex items-center justify-center flex-shrink-0">
              <Gem className="w-5 h-5 text-white" />
            </div>
            <div className="flex-1">
              <p className="text-sm font-semibold text-amber-900 dark:text-amber-300">
                {hiddenGem.message}
              </p>
              <Link
                href={`/profile/${hiddenGem.match.username}`}
                className="mt-2 flex items-center gap-2"
              >
                {hiddenGem.match.profileImage ? (
                  <img
                    src={hiddenGem.match.profileImage}
                    alt={hiddenGem.match.name}
                    className="w-8 h-8 rounded-full object-cover"
                  />
                ) : (
                  <div className="w-8 h-8 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white text-xs font-bold">
                    {hiddenGem.match.name.charAt(0)}
                  </div>
                )}
                <div>
                  <p className="text-sm font-medium text-gray-900 dark:text-white">{hiddenGem.match.name}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">{hiddenGem.match.headline || hiddenGem.match.college}</p>
                </div>
              </Link>
            </div>
          </div>
        </motion.div>
      )}
    </div>
  );
}
