'use client';

import { useEffect, useState, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, Users } from 'lucide-react';
import { getConnectionCelebration, type CelebrationData } from '@/lib/api/engagement';

/**
 * ConnectionCelebration - Full-screen confetti + celebration when connection accepted
 * Implements: Peak-End Rule (Peak moment) - Dopamine hit
 */

// Confetti particle component
function ConfettiParticle({ delay, x }: { delay: number; x: number }) {
  const colors = ['#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', '#FFEAA7', '#DDA0DD', '#FF69B4', '#00CED1'];
  const color = colors[Math.floor(Math.random() * colors.length)];
  const rotation = Math.random() * 360;
  const size = 6 + Math.random() * 8;

  return (
    <motion.div
      className="absolute top-0 pointer-events-none"
      initial={{ 
        x: x, 
        y: -20, 
        rotate: 0, 
        opacity: 1,
        scale: 1,
      }}
      animate={{ 
        y: typeof window !== 'undefined' ? window.innerHeight + 50 : 900,
        rotate: rotation + 720,
        opacity: [1, 1, 0.8, 0],
        x: x + (Math.random() - 0.5) * 200,
      }}
      transition={{ 
        duration: 2.5 + Math.random() * 1.5, 
        delay: delay,
        ease: 'easeIn',
      }}
      style={{
        width: size,
        height: size * (Math.random() > 0.5 ? 1 : 2.5),
        backgroundColor: color,
        borderRadius: Math.random() > 0.5 ? '50%' : '2px',
      }}
    />
  );
}

interface ConnectionCelebrationProps {
  connectionId: string | null;
  onClose: () => void;
}

export default function ConnectionCelebration({ connectionId, onClose }: ConnectionCelebrationProps) {
  const [celebration, setCelebration] = useState<CelebrationData | null>(null);
  const [showConfetti, setShowConfetti] = useState(false);

  useEffect(() => {
    if (!connectionId) return;

    const fetchCelebration = async () => {
      try {
        const data = await getConnectionCelebration(connectionId);
        if (data) {
          setCelebration(data);
          setShowConfetti(true);
          // Auto-dismiss after 5 seconds
          setTimeout(() => {
            onClose();
          }, 5000);
        }
      } catch (error) {
        console.error('Failed to fetch celebration:', error);
      }
    };

    fetchCelebration();
  }, [connectionId, onClose]);

  // Generate confetti particles
  const confettiCount = 60;
  const confettiParticles = Array.from({ length: confettiCount }, (_, i) => ({
    id: i,
    delay: Math.random() * 0.8,
    x: Math.random() * (typeof window !== 'undefined' ? window.innerWidth : 400),
  }));

  if (!connectionId || !celebration) return null;

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 z-[100] flex items-center justify-center bg-black/60"
        onClick={onClose}
      >
        {/* Confetti Layer */}
        {showConfetti && (
          <div className="fixed inset-0 overflow-hidden pointer-events-none">
            {confettiParticles.map((p) => (
              <ConfettiParticle key={p.id} delay={p.delay} x={p.x} />
            ))}
          </div>
        )}

        {/* Celebration Card */}
        <motion.div
          initial={{ scale: 0, rotate: -10 }}
          animate={{ scale: 1, rotate: 0 }}
          exit={{ scale: 0, opacity: 0 }}
          transition={{ type: 'spring', damping: 15, stiffness: 200 }}
          className="bg-white dark:bg-gray-900 rounded-3xl p-8 max-w-sm w-full mx-4 shadow-2xl text-center relative"
          onClick={(e) => e.stopPropagation()}
        >
          <button
            onClick={onClose}
            className="absolute top-4 right-4 p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-full"
          >
            <X className="w-5 h-5 text-gray-400" />
          </button>

          {/* Celebration emoji */}
          <motion.div
            initial={{ scale: 0 }}
            animate={{ scale: [0, 1.3, 1] }}
            transition={{ delay: 0.3, duration: 0.5 }}
            className="text-6xl mb-4"
          >
            üéâ
          </motion.div>

          {/* Profile images */}
          <div className="flex items-center justify-center gap-3 mb-4">
            {celebration.otherUser.profileImage ? (
              <motion.img
                initial={{ x: -30, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                transition={{ delay: 0.5 }}
                src={celebration.otherUser.profileImage}
                alt={celebration.otherUser.name}
                className="w-16 h-16 rounded-full border-4 border-green-400 object-cover"
              />
            ) : (
              <motion.div
                initial={{ x: -30, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                transition={{ delay: 0.5 }}
                className="w-16 h-16 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white text-2xl font-bold border-4 border-green-400"
              >
                {celebration.otherUser.name.charAt(0)}
              </motion.div>
            )}

            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ delay: 0.7 }}
              className="w-8 h-8 rounded-full bg-green-500 flex items-center justify-center"
            >
              <Users className="w-4 h-4 text-white" />
            </motion.div>
          </div>

          {/* Message */}
          <motion.h2
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.6 }}
            className="text-xl font-bold text-gray-900 dark:text-white mb-2"
          >
            {celebration.celebrationMessage}
          </motion.h2>

          {celebration.mutualConnections > 0 && (
            <motion.p
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.8 }}
              className="text-sm text-gray-500 dark:text-gray-400 mb-4"
            >
              You have {celebration.mutualConnections} mutual connection{celebration.mutualConnections > 1 ? 's' : ''}
            </motion.p>
          )}

          {/* Action button */}
          <motion.button
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 1 }}
            onClick={onClose}
            className="mt-2 px-6 py-2.5 bg-gradient-to-r from-blue-500 to-purple-500 text-white rounded-full font-medium hover:shadow-lg transition-shadow"
          >
            Continue Networking üöÄ
          </motion.button>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}
