'use client';

import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Flame, Trophy, Target, TrendingUp, X } from 'lucide-react';
import { getStreaks, type StreakData } from '@/lib/api/engagement';

/**
 * StreakCounter - Shows connection/login streak with fire animation
 * Implements: Streaks (Duolingo Effect) - Fear of loss
 */
export default function StreakCounter() {
  const [streak, setStreak] = useState<StreakData | null>(null);
  const [isExpanded, setIsExpanded] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStreaks = async () => {
      try {
        const data = await getStreaks();
        setStreak(data);
      } catch (error) {
        console.error('Failed to fetch streaks:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchStreaks();
  }, []);

  if (loading || !streak) return null;

  const hasStreak = streak.connectionStreak > 0;
  const isAtRisk = streak.isAtRisk;

  return (
    <>
      {/* Compact Streak Badge */}
      <motion.button
        onClick={() => setIsExpanded(true)}
        className="relative flex items-center gap-1.5 px-3 py-1.5 rounded-full bg-gradient-to-r from-orange-500 to-red-500 text-white text-sm font-bold shadow-lg hover:shadow-xl transition-shadow"
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
      >
        <motion.div
          animate={hasStreak ? { 
            scale: [1, 1.2, 1],
            rotate: [0, -5, 5, 0],
          } : {}}
          transition={{ repeat: Infinity, duration: 2 }}
        >
          <Flame className="w-4 h-4" />
        </motion.div>
        <span>{streak.connectionStreak}</span>

        {/* At-risk indicator */}
        {isAtRisk && hasStreak && (
          <motion.div
            className="absolute -top-1 -right-1 w-3 h-3 bg-yellow-400 rounded-full border-2 border-white"
            animate={{ scale: [1, 1.3, 1] }}
            transition={{ repeat: Infinity, duration: 1 }}
          />
        )}
      </motion.button>

      {/* Expanded Streak Panel */}
      <AnimatePresence>
        {isExpanded && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/40 z-50 flex items-center justify-center p-4"
            onClick={() => setIsExpanded(false)}
          >
            <motion.div
              initial={{ scale: 0.8, opacity: 0 }}
              animate={{ scale: 1, opacity: 1 }}
              exit={{ scale: 0.8, opacity: 0 }}
              className="bg-white dark:bg-gray-900 rounded-2xl p-6 max-w-sm w-full shadow-2xl"
              onClick={(e) => e.stopPropagation()}
            >
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-bold text-gray-900 dark:text-white">Your Streaks</h3>
                <button onClick={() => setIsExpanded(false)} className="p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-full">
                  <X className="w-5 h-5 text-gray-500" />
                </button>
              </div>

              {/* Connection Streak */}
              <div className="space-y-4">
                <div className="flex items-center gap-3 p-3 bg-gradient-to-r from-orange-50 to-red-50 dark:from-orange-900/20 dark:to-red-900/20 rounded-xl">
                  <div className="w-12 h-12 rounded-full bg-gradient-to-br from-orange-500 to-red-500 flex items-center justify-center">
                    <Flame className="w-6 h-6 text-white" />
                  </div>
                  <div className="flex-1">
                    <p className="font-bold text-2xl text-gray-900 dark:text-white">
                      {streak.connectionStreak} day{streak.connectionStreak !== 1 ? 's' : ''}
                    </p>
                    <p className="text-sm text-gray-500 dark:text-gray-400">Networking streak</p>
                  </div>
                  {isAtRisk && (
                    <span className="px-2 py-1 bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400 text-xs rounded-full font-medium">
                      At risk!
                    </span>
                  )}
                </div>

                {/* Login Streak */}
                <div className="flex items-center gap-3 p-3 bg-blue-50 dark:bg-blue-900/20 rounded-xl">
                  <div className="w-10 h-10 rounded-full bg-blue-500 flex items-center justify-center">
                    <TrendingUp className="w-5 h-5 text-white" />
                  </div>
                  <div>
                    <p className="font-bold text-lg text-gray-900 dark:text-white">{streak.loginStreak} days</p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">Login streak</p>
                  </div>
                </div>

                {/* Records */}
                <div className="grid grid-cols-2 gap-3">
                  <div className="p-3 bg-gray-50 dark:bg-gray-800 rounded-xl text-center">
                    <Trophy className="w-5 h-5 text-yellow-500 mx-auto mb-1" />
                    <p className="font-bold text-gray-900 dark:text-white">{streak.longestConnectionStreak}</p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">Best streak</p>
                  </div>
                  <div className="p-3 bg-gray-50 dark:bg-gray-800 rounded-xl text-center">
                    <Target className="w-5 h-5 text-green-500 mx-auto mb-1" />
                    <p className="font-bold text-gray-900 dark:text-white">
                      {streak.weeklyConnectionsMade}/{streak.weeklyConnectionsGoal}
                    </p>
                    <p className="text-xs text-gray-500 dark:text-gray-400">This week</p>
                  </div>
                </div>

                {/* Motivational message */}
                {isAtRisk && hasStreak && (
                  <motion.div
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    className="p-3 bg-yellow-50 dark:bg-yellow-900/20 rounded-xl border border-yellow-200 dark:border-yellow-800"
                  >
                    <p className="text-sm text-yellow-800 dark:text-yellow-300 font-medium">
                      ‚ö†Ô∏è Don&apos;t break your {streak.connectionStreak}-day streak! Connect with someone new today.
                    </p>
                  </motion.div>
                )}

                {streak.connectionStreak >= 5 && (
                  <div className="text-center text-sm text-gray-500 dark:text-gray-400">
                    üî• You&apos;ve connected with someone new for {streak.connectionStreak} days straight!
                  </div>
                )}
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}
