'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Lock, Clock, Zap } from 'lucide-react';
import { getConnectionLimit, type ConnectionLimit } from '@/lib/api/engagement';

/**
 * ConnectionLimitBanner - Shows remaining daily connection requests
 * Implements: Scarcity + Urgency
 * "Only 3 connection requests left today!"
 */
export default function ConnectionLimitBanner() {
  const [limit, setLimit] = useState<ConnectionLimit | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchLimit = async () => {
      try {
        const data = await getConnectionLimit();
        setLimit(data);
      } catch (error) {
        console.error('Failed to fetch connection limit:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchLimit();
  }, []);

  if (loading || !limit) return null;

  // Don't show if plenty of requests remaining
  if (limit.remaining > 5) return null;

  const percentage = (limit.remaining / limit.limit) * 100;
  const isLow = limit.remaining <= 3;
  const isDepleted = limit.remaining === 0;

  // Calculate time until reset
  const resetTime = new Date(limit.resetsAt);
  const now = new Date();
  const hoursUntilReset = Math.ceil((resetTime.getTime() - now.getTime()) / (1000 * 60 * 60));

  return (
    <motion.div
      initial={{ opacity: 0, y: -10 }}
      animate={{ opacity: 1, y: 0 }}
      className={`rounded-xl p-3 border ${
        isDepleted
          ? 'bg-red-50 dark:bg-red-900/20 border-red-200 dark:border-red-800'
          : isLow
          ? 'bg-yellow-50 dark:bg-yellow-900/20 border-yellow-200 dark:border-yellow-800'
          : 'bg-blue-50 dark:bg-blue-900/20 border-blue-200 dark:border-blue-800'
      }`}
    >
      <div className="flex items-center gap-2">
        {isDepleted ? (
          <Lock className="w-4 h-4 text-red-500 flex-shrink-0" />
        ) : isLow ? (
          <Zap className="w-4 h-4 text-yellow-500 flex-shrink-0" />
        ) : (
          <Zap className="w-4 h-4 text-blue-500 flex-shrink-0" />
        )}

        <div className="flex-1">
          <p className={`text-xs font-medium ${
            isDepleted
              ? 'text-red-700 dark:text-red-400'
              : isLow
              ? 'text-yellow-700 dark:text-yellow-400'
              : 'text-blue-700 dark:text-blue-400'
          }`}>
            {isDepleted
              ? `Daily limit reached ‚Äî resets in ${hoursUntilReset}h`
              : `${limit.remaining} connection request${limit.remaining !== 1 ? 's' : ''} left today`}
          </p>

          {/* Progress bar showing depletion */}
          <div className="mt-1.5 w-full bg-gray-200 dark:bg-gray-700 rounded-full h-1.5">
            <motion.div
              className={`h-1.5 rounded-full ${
                isDepleted
                  ? 'bg-red-500'
                  : isLow
                  ? 'bg-yellow-500'
                  : 'bg-blue-500'
              }`}
              initial={{ width: '100%' }}
              animate={{ width: `${percentage}%` }}
              transition={{ duration: 0.5 }}
            />
          </div>
        </div>

        {isDepleted && (
          <div className="flex items-center gap-1 text-xs text-gray-500 dark:text-gray-400 flex-shrink-0">
            <Clock className="w-3 h-3" />
            <span>{hoursUntilReset}h</span>
          </div>
        )}
      </div>
    </motion.div>
  );
}
