'use client';

import { useState, useEffect, useCallback } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, Rocket, Star, PartyPopper } from 'lucide-react';
import { getSessionSummary, type SessionSummary } from '@/lib/api/engagement';

/**
 * ExitMessage - Shows positive summary when user leaves
 * Implements: Peak-End Rule (Memory Hack) - End on a positive
 * "You made 2 new connections today! See you tomorrow üöÄ"
 */
export default function ExitMessage() {
  const [summary, setSummary] = useState<SessionSummary | null>(null);
  const [isVisible, setIsVisible] = useState(false);

  const showExitMessage = useCallback(async () => {
    try {
      const data = await getSessionSummary();
      setSummary(data);
      setIsVisible(true);
    } catch (error) {
      console.error('Failed to fetch session summary:', error);
    }
  }, []);

  useEffect(() => {
    // Detect when user is about to leave (beforeunload) ‚Äî limited on mobile
    // Instead, show after a period of inactivity or on visibility change
    let inactivityTimer: NodeJS.Timeout;

    const handleVisibilityChange = () => {
      if (document.visibilityState === 'hidden') {
        // User is switching away from the app
        showExitMessage();
      }
    };

    const resetInactivity = () => {
      clearTimeout(inactivityTimer);
      setIsVisible(false);
      inactivityTimer = setTimeout(() => {
        // Show after 5 minutes of inactivity
        showExitMessage();
      }, 5 * 60 * 1000);
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);
    document.addEventListener('mousemove', resetInactivity);
    document.addEventListener('touchstart', resetInactivity);

    resetInactivity();

    return () => {
      clearTimeout(inactivityTimer);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
      document.removeEventListener('mousemove', resetInactivity);
      document.removeEventListener('touchstart', resetInactivity);
    };
  }, [showExitMessage]);

  if (!summary) return null;

  const emojiMap: Record<string, React.ReactNode> = {
    'üéâ': <PartyPopper className="w-8 h-8 text-yellow-500" />,
    'üöÄ': <Rocket className="w-8 h-8 text-blue-500" />,
    'üåü': <Star className="w-8 h-8 text-yellow-400" />,
  };

  return (
    <AnimatePresence>
      {isVisible && (
        <motion.div
          initial={{ opacity: 0, y: 100 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 100 }}
          className="fixed bottom-20 left-1/2 -translate-x-1/2 z-50 w-[90%] max-w-md"
        >
          <div className="bg-white dark:bg-gray-900 rounded-2xl p-5 shadow-2xl border border-gray-200 dark:border-gray-700">
            <button
              onClick={() => setIsVisible(false)}
              className="absolute top-3 right-3 p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded-full"
            >
              <X className="w-4 h-4 text-gray-400" />
            </button>

            <div className="flex flex-col items-center text-center">
              <motion.div
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ type: 'spring', delay: 0.2 }}
              >
                {emojiMap[summary.emoji] || <Star className="w-8 h-8 text-yellow-400" />}
              </motion.div>

              <motion.p
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: 0.3 }}
                className="mt-3 text-sm font-medium text-gray-900 dark:text-white"
              >
                {summary.message}
              </motion.p>

              {/* Stats row */}
              {(summary.connectionsAccepted > 0 || summary.newPosts > 0 || summary.messagesCount > 0) && (
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ delay: 0.5 }}
                  className="flex items-center gap-4 mt-3 text-xs text-gray-500 dark:text-gray-400"
                >
                  {summary.connectionsAccepted > 0 && (
                    <span>ü§ù {summary.connectionsAccepted} connection{summary.connectionsAccepted > 1 ? 's' : ''}</span>
                  )}
                  {summary.newPosts > 0 && (
                    <span>üìù {summary.newPosts} post{summary.newPosts > 1 ? 's' : ''}</span>
                  )}
                  {summary.messagesCount > 0 && (
                    <span>üí¨ {summary.messagesCount} message{summary.messagesCount > 1 ? 's' : ''}</span>
                  )}
                </motion.div>
              )}
            </div>
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
